/**
 * 
 */
export function getDate(before) {
    var time = new Date();
    time.setDate(time.getDate() + before); //Day 
    var y = time.getFullYear();
    var m = time.getMonth() + 1; // 
    var d = time.getDate();
    m = m > 9 ? m : '0' + m;;
    d = d > 9 ? d : '0' + d;
    return y + "-" + m + "-" + d;
}